.learning-holder {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto 80px;
  display: flex;
  justify-content: space-between;
  @include response(992px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.learning-content {
  max-width: 750px;
  width: 100%;
  .content-area {
    margin-bottom: 50px;
    p {
      margin: 0 0 30px;
      line-height: 175%;
    }
    ul {
      padding: 0 20px;
      margin: 0;
      list-style: none;
      li {
        position: relative;
        padding-left: 24px;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 2px;
          width: 16px;
          height: 24px;
          background-image: url("../data/list-marker.svg");
        }
      }
      h2,h3,h4,h5,h6 {
        margin: 0 0 10px;
      }
    }
  }
  @include response(992px) {
    max-width: 100%;
  }
}
.learning-aside {
  max-width: 340px;
  width: 100%;
  margin-left: 30px;
  position: relative;
  padding-bottom: 50px;
  @include response(992px) {
    margin: 0 auto;
    .featured-bottles {
      display: none;
    }
  }
  &__inner {
    position: sticky;
    top: 100px;
    left: 0;
  }
}
.learning-nav {
  padding: 0;
  margin: 0 0 50px;
  list-style: none;
  border-radius: 5px;
  border: 2px solid #DEB992;
  overflow: hidden;
  &-item {
    display: flex;
    align-items: center;
    padding: 20px;
    gap: 14px;
    border-bottom: 1px solid #fff;
    background: #f9f9fa;
    text-decoration: none;
    &:hover,
    &.active {
      background: #DEB992;
      border-color: #DEB992;
    }
    @include transition();
    &__label {
      font-size: 12px;
      line-height: 125%;
      color: #5A5E62;
    }
    &__title {
      font-family: 'Playfair Display', sans-serif;
      font-weight: 700;
      font-size: 22px;
      line-height: 125%;
      color: #0B1014;
    }
  }
}
.agave-item {
  background: #f9f9fa;
  margin-bottom: 30px;
  &__img {
    padding: 30px 30px 40px;
    position: relative;
    margin-bottom: 40px;
    display: flex;
    justify-content: center;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 40px;
      background-image: url("../data/agave-line-2.png");
    }
  }
  &:nth-child(even) {
    .agave-item__img::after {
      background-image: url("../data/agave-line-1.png");
    }
  }
  &__inner {
    padding: 0 30px 30px;
  }
  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 25px;
    text-align: center;
    margin-bottom: 20px;
  }
  &__content {
    p {
      margin: 0 0 30px;
      font-size: 16px;
      line-height: 175%;
      &:last-child {
        margin: 0;
      }
    }
  }
}

.what-is-mezcal {
  overflow: visible;
}

.featured-bottles {
  &__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 125%;
    margin-bottom: 18px;
  }
  .bottles-list {
    margin: 0 -8px;
    .best-products__item {
      max-width: calc(50% - 16px);
      flex-basis: calc(50% - 16px);
      margin: 0 8px 16px;
      padding: 20px 16px 0;
      &-rate {
        font-size: 14px;
      }
      .rate__item {
        width: 12px;
      }
    }
  }
}
.aging-item {
  padding: 30px 40px;
  background: #DEB992;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  @include response(767px) {
    padding: 20px 15px;
  }
  &__inner {
    display: flex;
    align-items: center;
    gap: 35px;
    margin-bottom: 24px;
    @include response(767px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__footer {
    display: flex;
    justify-content: center;
  }
  &__left {
    padding-left: 80px;
    position: relative;
    @include response(767px) {
      padding: 0;
    }
  }
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 125%;
    transform: rotate(-90deg) translate(-2%, -138%);
    position: absolute;
    left: 0;
    top: 31px;
    width: 200px;
    transform-origin: 50% 50%;
    text-align: center;
    @include response(767px) {
      transform: none;
      position: static;
    }
  }
  &__img {
    @include response(767px) {
      display: none;
    }
  }
  &__right {
    display: flex;
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
    @include response(767px) {
      flex-wrap: wrap;
    }
  }
  &__info {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
    padding: 20px 25px;
    padding-right: 0;
    background: #fff;
    @include response(767px) {
      padding: 10px;
      justify-content: space-between;
    }
    &-block {
      width: 100%;
      max-width: calc(50% - 25px);
      &-title {
        font-weight: 700;
        font-size: 15px;
        line-height: 15px;
        margin-bottom: 5px;
      }
      &-content {
        font-style: italic;
        font-weight: 500;
        font-size: 13px;
        line-height: 13px;
        color: #5A5E62;
      }
    }
  }
  &__usage {
    max-width: 164px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #000;
    color: #DEB992;
    padding: 20px 15px;
    font-size: 12px;
    text-align: center;
    svg {
      margin-bottom: 12px;
    }
    @include response(767px) {
      max-width: 100%;
      width: 100%;
      flex-direction: row;
      justify-content: center;
      text-align: left;
      gap: 15px;
      .aging-item__usage-text {
        max-width: 190px;
      }
    }
  }
  &.yellow {
    background: #EF9D1B;
    .aging-item__usage {
      color: #EF9D1B;
      svg path {
        fill: #EF9D1B;
      }
    }
  }
  &.orange {
    background: #D85D19;
    .aging-item__usage {
      color: #D85D19;
      svg path {
        fill: #D85D19;
      }
    }
  }
}
.mezcal-table {
  overflow: hidden;
  border-radius: 5px;
  table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    border: 1px solid #EBEBEE;
    td {
      padding: 19px;
      font-size: 12px;
    }
    tbody tr:nth-child(odd) {
      td {
        background: #f9f9fa;
      }
    }
    thead {
      td {
        font-weight: 700;
        font-size: 12px;
        background: #DEB992;
        border-right: 1px solid #fff;
        padding: 16px;
        &:last-child {
          border: none;
        }
      }
    }
  }
}
.learning-author {
  padding: 80px 15px;
}
.learning-stories {
  .stories__head-title {
    text-align: left;
  }
  .stories__list {
    margin-bottom: 0;
  }
  .stories__inner {
    margin: 0 auto 100px;
  }
  @include response(767px) {
    .stories__item {
      max-width: 260px !important;
      flex-basis: 260px !important;
    }
  }
}

.type-item {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  padding: 30px;
  background: rgba(222, 185, 146, 0.14);
  border: 2px solid #DEB992;
  border-radius: 5px;
  margin-bottom: 30px;
  &__thumb {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-right: 30px;
    margin-right: 30px;
    border-right: 2px solid #DEB992;
    max-width: 145px;
    width: 100%;
  }
  &__title {
    font-weight: 600;
    font-size: 16px;
    margin-top: 15px;
  }
  &__content {
    line-height: 175%;
  }
}